<template>
  <transition
    name="header"
    mode="out-in"
    v-on:enter="startAnimate"
    v-on:before-enter="beforeAnimate"
    v-on:leave="leaveAnimate" >
    <div class="slideContent offScreen">
        <div class="content">
            <h1 class="title">Комплекс термической <br>резки «Енисей»</h1>
            <div class="about">
                <div class="rotateTitle">О Ктрм</div>
                <p class="text">{{text}}</p>
            </div>
        </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'FirstSlide',
  data() {
    return {
      text: 'Комплексы термической резки металлов «Енисей» предназначены для раскроя черного и цветного  листового металлов по контуру, запрограммированному в управляющей программе.',
    };
  },
  methods: {
    startAnimate(el, done) {
      setTimeout(() => {
        $(this.$el).toggleClass('onScreen offScreen');
      }, 500);
      done();
    },

    leaveAnimate(el, done) {
      $(this.$el).toggleClass('onScreen offScreen');
      setTimeout(() => {
        done();
      }, 700);
    },

    beforeAnimate() {
      $(this.$el).removeClass('onScreen');
      $(this.$el).removeClass('onScreen');
    },
  }
};
</script>

<style lang="scss" scoped>

    .title {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 5vw;
        font-weight: 400;
        font-family: arame;
        margin: 0;
        width: 65%;
        transition: all .35s;
    }

    .slideContent {
        background-color: rgba(0, 0, 0, 0.15);
        position: relative;

        &.offScreen {
          .title {
            transform: translateY(-100%);
            opacity: 0;
          }

          .about {
            transform: translateY(50%);
            opacity: 0;
          }
        }

        &.onScreen {
          .title {
            transform: translateY(-50%);
            opacity: 1;
          }

          .about {
            transform: translateY(0%);
            opacity: 1;
          }
        }
    }

    .content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 70%;
        height: 80%;
    }

    .about {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 402px;
        display: flex;
        justify-content: space-between;
        transition: all .35s;
    }

    .rotateTitle {
        text-transform: uppercase;
        letter-spacing: 3px;
        transform: rotate(90deg);
        transform-origin: top;
        font-weight: 600;
        position: absolute;
        top: 35px;
        left: -42px;
        opacity: 0.6;
        font-size: 10px;
    }

    .text {
        margin: 0;
        margin-left: 40px;
        line-height: 24px;
        letter-spacing: 2px;
        font-weight: 600;
    }

</style>



